* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  line-height: 1.6;
}

.navbar {
  background: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

.navbar .logo {
  width: auto;
  height: 120px;
}

.nav-links {
  gap: 1.5rem;
  list-style: none;
  display: flex;
}

.nav-links a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

.nav-links a:hover {
  color: #0ab4ff;
}

.hero {
  color: #fff;
  text-align: center;
  background-image: url("coming-soon-bg.b6173654.jpg");
  background-position: center;
  background-size: cover;
  padding: 100px 20px;
  position: relative;
}

.hero:before {
  content: "";
  z-index: 1;
  background: #00000080;
  position: absolute;
  inset: 0;
}

.hero > * {
  z-index: 2;
  position: relative;
}

.hero h1 {
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.hero p {
  margin-bottom: 2rem;
  font-size: 1.2rem;
}

.hero-cta-section {
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-top: 2rem;
  display: flex;
}

.phone-number {
  color: #fff;
  background: #0006;
  border: 2px solid #fff;
  border-radius: 8px;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s;
}

.phone-number:hover {
  background: #fff3;
  transform: scale(1.05);
}

.cta-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.cta-btn {
  border-radius: 8px;
  padding: 1rem 2.5rem;
  font-size: 1.1rem;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.cta-btn.primary {
  color: #fff;
  background: #ff6b35;
  box-shadow: 0 4px 15px #ff6b3566;
}

.cta-btn.primary:hover {
  background: #ff5722;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #ff6b3599;
}

.cta-btn.secondary {
  color: #036;
  background: #fff;
  border: 2px solid #fff;
}

.cta-btn.secondary:hover {
  color: #fff;
  background: #0ab4ff;
  border-color: #0ab4ff;
}

.content-background {
  background-image: url("center background image 2.8608d0a5.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.section {
  text-align: center;
  max-width: 1000px;
  margin: auto;
  padding: 80px 20px;
}

.section h2, .section p {
  color: #fff;
  background: #0009;
  border-radius: 8px;
  max-width: fit-content;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  display: block;
}

.section h2 {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.section p {
  margin-bottom: 1rem;
}

.training-section .cta-btn {
  max-width: fit-content;
  margin-bottom: 1.5rem;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.training-list {
  text-align: left;
  color: #fff;
  background: #0009;
  border-radius: 8px;
  max-width: 500px;
  margin: 1.5rem auto;
  padding: 20px 30px;
  list-style: inside;
}

.training-list li {
  padding: 8px 0;
  font-size: 1.1rem;
}

.cards {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 1.5rem;
  margin-top: 2rem;
  display: flex;
}

.card {
  background: #f4f4f4;
  border-radius: 8px;
  flex: 250px;
  padding: 2rem;
  box-shadow: 0 2px 5px #0000001a;
}

.contact-section form {
  flex-direction: column;
  gap: 1rem;
  max-width: 500px;
  margin: auto;
  display: flex;
}

.contact-section input, .contact-section textarea, .contact-section button {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: .8rem;
}

.contact-section button {
  color: #fff;
  cursor: pointer;
  background: #0ab4ff;
  border: none;
  font-weight: bold;
}

.contact-section button:hover {
  background: #0077b3;
}

.success-message {
  color: #fff;
  text-align: center;
  background: #10b981;
  border-radius: 8px;
  max-width: 500px;
  margin: 0 auto 1rem;
  padding: 20px 30px;
}

.success-message p {
  background: none;
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
}

footer {
  text-align: center;
  color: #333;
  background: linear-gradient(#d5d5d5 0%, silver 50%, #a8a8a8 100%) 0 0 / 100% 200%;
  border-top: 1px solid #e8e8e8;
  padding: 2rem;
  position: relative;
  box-shadow: inset 0 1px #ffffff80, inset 0 -1px #0000001a;
}

footer:before {
  content: "";
  pointer-events: none;
  background: repeating-linear-gradient(90deg, #0000, #0000 2px, #ffffff1a 2px 4px);
  position: absolute;
  inset: 0;
}

footer p {
  z-index: 1;
  text-shadow: 0 1px #ffffff80;
  position: relative;
}

html, body {
  height: 100%;
  margin: 0;
  font-family: system-ui, Segoe UI, Roboto, Arial;
}

header {
  color: #fff;
  background: #036;
  align-items: center;
  gap: 12px;
  padding: 24px;
  display: flex;
}

#logo {
  object-fit: contain;
  width: 128px;
  height: 128px;
}

@media (width <= 600px) {
  .navbar .logo, #logo {
    width: auto;
    height: 64px;
  }

  .hero h1 {
    font-size: 1.8rem;
  }

  .phone-number {
    padding: .8rem 1.5rem;
    font-size: 1.2rem;
  }

  .cta-buttons {
    flex-direction: column;
    width: 100%;
  }

  .cta-btn {
    text-align: center;
    width: 100%;
  }
}

main {
  padding: 24px;
}
