/* Reset & base */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
}

body {
    line-height: 1.6;
    color: #333;
}

/* NAVBAR */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background: #1a1a1a;
}

.navbar .logo {
    height: 120px;
    width: auto;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 1.5rem;
}

.nav-links a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
}

.nav-links a:hover {
    color: #0ab4ff;
}

/* HERO */
.hero {
    background-image: url('./images/coming-soon-bg.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
    color: white;
    text-align: center;
    padding: 100px 20px;
}

.hero::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
}

.hero > * {
    position: relative;
    z-index: 2;
}

.hero h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.hero p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
}

.hero-cta-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    margin-top: 2rem;
}

.phone-number {
    font-size: 1.5rem;
    font-weight: bold;
    color: #fff;
    text-decoration: none;
    background: rgba(0, 0, 0, 0.4);
    padding: 1rem 2rem;
    border-radius: 8px;
    border: 2px solid #fff;
    transition: all 0.3s ease;
}

.phone-number:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: scale(1.05);
}

.cta-buttons {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
}

.cta-btn {
    padding: 1rem 2.5rem;
    text-decoration: none;
    font-weight: bold;
    border-radius: 8px;
    font-size: 1.1rem;
    transition: all 0.3s ease;
    display: inline-block;
}

.cta-btn.primary {
    background: #ff6b35;
    color: #fff;
    box-shadow: 0 4px 15px rgba(255, 107, 53, 0.4);
}

.cta-btn.primary:hover {
    background: #ff5722;
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(255, 107, 53, 0.6);
}

.cta-btn.secondary {
    background: #fff;
    color: #003366;
    border: 2px solid #fff;
}

.cta-btn.secondary:hover {
    background: #0ab4ff;
    color: #fff;
    border-color: #0ab4ff;
}

/* SECTION STYLES */
.content-background {
    background-image: url('./images/center background image 2.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.section {
    padding: 80px 20px;
    max-width: 1000px;
    margin: auto;
    text-align: center;
}

.section h2,
.section p {
    background: rgba(0, 0, 0, 0.6);
    display: block;
    padding: 10px 20px;
    border-radius: 8px;
    color: #fff;
    max-width: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.section h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.section p {
    margin-bottom: 1rem;
}

/* TRAINING SECTION */
.training-section .cta-btn {
    margin-bottom: 1.5rem;
    display: block;
    max-width: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.training-list {
    list-style: disc;
    list-style-position: inside;
    text-align: left;
    max-width: 500px;
    margin: 1.5rem auto;
    background: rgba(0, 0, 0, 0.6);
    padding: 20px 30px;
    border-radius: 8px;
    color: #fff;
}

.training-list li {
    padding: 8px 0;
    font-size: 1.1rem;
}

.cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 2rem;
}

.card {
    background: #f4f4f4;
    padding: 2rem;
    border-radius: 8px;
    flex: 1 1 250px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

/* CONTACT FORM */
.contact-section form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 500px;
    margin: auto;
}

.contact-section input, 
.contact-section textarea, 
.contact-section button {
    padding: 0.8rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.contact-section button {
    background: #0ab4ff;
    color: #fff;
    border: none;
    font-weight: bold;
    cursor: pointer;
}

.contact-section button:hover {
    background: #0077b3;
}

.success-message {
    background: #10b981;
    color: #fff;
    padding: 20px 30px;
    border-radius: 8px;
    max-width: 500px;
    margin: 0 auto 1rem auto;
    text-align: center;
}

.success-message p {
    background: transparent;
    padding: 0;
    margin: 0;
    font-size: 1.2rem;
}

/* FOOTER */
footer {
    text-align: center;
    padding: 2rem;
    background: linear-gradient(180deg, #d5d5d5 0%, #c0c0c0 50%, #a8a8a8 100%);
    background-size: 100% 200%;
    border-top: 1px solid #e8e8e8;
    box-shadow: inset 0 1px 0 rgba(255,255,255,0.5), inset 0 -1px 0 rgba(0,0,0,0.1);
    color: #333;
    position: relative;
}

footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: repeating-linear-gradient(
        90deg,
        transparent,
        transparent 2px,
        rgba(255,255,255,0.1) 2px,
        rgba(255,255,255,0.1) 4px
    );
    pointer-events: none;
}

footer p {
    position: relative;
    z-index: 1;
    text-shadow: 0 1px 0 rgba(255,255,255,0.5);
}
/* Minimal styles */
html,body{height:100%;margin:0;font-family:system-ui,Segoe UI,Roboto,Arial}
header{display:flex;align-items:center;gap:12px;padding:24px;background:#003366;color:#fff}
#logo{width:128px;height:128px;object-fit:contain}

/* Responsive cap for small screens */
@media (max-width: 600px) {
    .navbar .logo, #logo {
        height: 64px;
        width: auto;
    }
    
    .hero h1 {
        font-size: 1.8rem;
    }
    
    .phone-number {
        font-size: 1.2rem;
        padding: 0.8rem 1.5rem;
    }
    
    .cta-buttons {
        flex-direction: column;
        width: 100%;
    }
    
    .cta-btn {
        width: 100%;
        text-align: center;
    }
}
main{padding:24px}
